export const settingsModule = {
  namespaced: true,
  state: {
    dark: true,
    theme: {
      primary: "#456CB2",
      secondary: "#424242",
      accent: "#82B1FF"
    },
    user: {
      name: "Max Mustermann"
    },
    language: "de"
  },
  mutations: {
    setDark(state, isDark) {
      state.dark = isDark;
    },
    setLanguage(state, locale) {
      state.language = locale;
    },
    setTheme(state, theme) {
      state.theme = theme;
    }
  },
  getters: {
    theme(state) {
      return {
        dark: state.dark,
        ...state.theme
      };
    },
    user(state) {
      return state.user;
    },
    language(state) {
      return state.language;
    }
  }
};
