const themeUpdater = {
  watch: {
    currentTheme: {
      handler() {
        this.$vuetify.theme.primary = this.currentTheme.primary;
        this.$vuetify.theme.secondary = this.currentTheme.secondary;
        this.$vuetify.theme.accent = this.currentTheme.accent;
      },
      immediate: true
    }
  },
  computed: {
    currentTheme() {
      return this.$store.getters["settings/theme"];
    }
  }
};

export { themeUpdater };
