<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  data() {
    return {
      e1: 0
    };
  },
  computed: {
    ...mapGetters({
      language: "settings/language",
      theme: "settings/theme"
    })
  },
  methods: {
    ...mapMutations({
      setLanguage: "settings/setLanguage",
      setDark: "settings/setDark"
    })
  }
